body {
  margin: 0;
  padding: 0;
}

.fullspace-master {
  position: absolute;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
}

.master {
  height: 100%;
  align-items: center;
  justify-content: center;
  display: -ms-grid;
  display: grid;
  grid-gap: 0.3%;
  -ms-grid-columns: 80%;
      grid-template-columns: 80%;
}

.art {
  color: none;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 1fr;
      grid-template-columns: 1fr;
  grid-gap: 0.4%;
  width: 91%;
  height: 100%;
  object-fit: contain;
  -ms-grid-column-align: center;
      justify-self: center;
  -ms-grid-row-align: center;
      align-self: center;
}

.art-frame {
  border: 10px solid black;
  background-color: white;
  margin: 2%;
  padding: 5%;
  box-shadow: 10px 10px 10px 10px #6e6c69, 0px 0px 20px 0px rgba(0, 0, 0, 0.4) inset;
}

img {
  width: 100%;
  max-height: 90%;
  object-fit: contain;
  -ms-grid-row-align: center;
  align-self: center;
  -ms-grid-column-align: center;
      justify-self: center;
  cursor: pointer;
}

.plaque-wording-div {
  background-color: #ebecf1;
      grid-template-areas: exp;
  -ms-grid-row-align: center;
  align-self: center;
  -ms-grid-column-align: center;
      justify-self: center;
  width: 75%;
  margin-top: 5%;
