}

.floor {
  background-image: url(data:image/jpeg;base64,);
  object-fit: scale-down;
  background-repeat: repeat;
  width: 100%;
  height: 20%;
  -ms-grid-column-align: end;
      justify-self: end;
  -ms-grid-row-align: end;
  align-self: end;
}

@media screen and (min-width: 750px) {
  .master {
    height: 100%;
    align-items: center;
    display: -ms-grid;
    display: grid;
    grid-gap: 0.3%;
    -ms-grid-columns: 300px 5fr;
        grid-template-columns: 300px 5fr;
  }
  .art {
    max-height: 100%;
    align-items: center;
    display: -ms-grid;
    display: grid;
    grid-gap: 0.3%;
    -ms-grid-columns: (minmax(200px, 5fr))[auto-fill];
        grid-template-columns: repeat(auto-fill, minmax(200px, 5fr));
    justify-items: center;
  }
  .floor {
    -ms-grid-column: 1;
    -ms-grid-column-span: 3;
    grid-column: 1 / 4;
    background-image: url(data:image/jpeg;base64,);
    object-fit: scale-down;
    background-repeat: repeat;
    width: 100%;
    height: 20%;
    -ms-grid-column-align: end;
        justify-self: end;
    -ms-grid-row-align: end;
    align-self: end;
  }
}
