.master-return-letter {
  padding: 0px;
  margin: 0px;
  position: relative;
  width: 90%;
  height: 95px;
}

.letter-top-triangle {
  position: absolute;
  padding: 0px;
  margin: 0px;
  height: 100%;
  width: 100%;
  background-image: url(/Artistry/static/media/envelope-lid-large.b8785872.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top;
  z-index: 4;
  transition: 1.7s 2s ease-in;
  transform-origin: top;
  transform: rotateX(0deg);
}

.letter-top-triangle-open {
  position: absolute;
  padding: 0px;
  margin: 0px;
  height: 100%;
  width: 100%;
  background-image: url(/Artistry/static/media/envelope-lid-large.b8785872.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
  z-index: 0;
  transition: 1.7s ease-in;
  transform-origin: top;
  transform: rotateX(180deg);
}

.envelope {
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: url(/Artistry/static/media/recovers.24af8733.png);
  background-size: cover;
  background-position: cover;
  background-repeat: no-repeat;
  z-index: 3;
}

.inside-letter {
  position: absolute;
  width: 100%;
  height: 100%;
  max-height: 100%;
  top: 0px;
  background-image: url(/Artistry/static/media/letter-full-size2.2b107f9d.jpg);
  background-size: cover;
  z-index: 1;
  transition: 2.5s 0s ease-in;
  overflow: hidden;
}

.inside-letter-open {
  width: 100%;
  height: 110%;
  max-height: 110%;
  top: -100%;
  position: absolute;
  background-image: url(/Artistry/static/media/letter-full-size2.2b107f9d.jpg);
  background-size: cover;
  z-index: 2;
  transition: 3s 0.5s ease-in;
  overflow: hidden;
  text-overflow: ellipsis;
}

.challenge-p {
  color: #271c04;
  font-family: "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  font-size: 0.9em;
  text-align: center;
